
.App{
	height: 100vh;

	display: grid;

	grid-template-columns: 1fr;

	grid-gap: 20px;

	grid-auto-rows: 1fr 50px;
}

a{
	text-decoration: none;
}

a:hover{
     text-decoration:none; 
     cursor:pointer;  
}

span{
	position: absolute;
	top: 0;
	left: 0;
}

.pantalla{

	display: grid;

	grid-template-columns: 1fr;

	grid-gap: 10px;

	grid-auto-rows: repeat(4, 100px);

	padding-left: 20px;

	padding-top: 20px;

}

.barra{

	background: black;

	width: 100%;

	min-height: 50px;

	opacity: 0.8;

	display: flex;

	align-items: center;
}

.icono img{
	width: 30px;
	margin-left: 15px;
	margin-right: 20px;
}

.programa{
	height: 100px;
	width: 100px;
	background:rgba(0,0,0,0.7);
	color: white;

	display: flex;

	flex-direction: column;

	align-items: center;

	justify-content: center;
}

.programa:hover{
	border: 2px solid #2a2a2b;
}

.programa img{
	width: 60px;
	opacity: 1;
}


.contenedorPrograma{
	
	position: absolute;

	top: 0;

	left: 100px;

	width: 400px;

	min-height: 400px;

	background: white;

}

.draggable {
	touch-action: none;
	user-select: none;
}


textarea{
	width: 350px; 
	height: 350px;
	resize: none;
	background: yellow;
}


.card-header{
	text-align: right;
}

.card-header *{
	margin-left: 10px;
}











