

.draggable3 {
	touch-action: none;
	user-select: none;
}

.contenedorPrograma{
	
	position: absolute;

	top: 0;

	left: 100px;

	width: 400px;

	min-height: 400px;

	background: white;

}

.resultado{
	grid-area: resultado;
	background: black;
	color: white;
	text-align: right;
	padding-right: 20px;
	padding-top: 20px;
}

.ce{
	grid-area: ce;
}

.dividir{
	grid-area: dividir;
}

.multiplicar{
	grid-area: multiplicar;
}

.restar{
	grid-area: restar;
}

.sumar{
	grid-area: sumar;
}

.igual{
	grid-area: igual;
}

.cero{
	grid-area: cero;
}

.uno{
	grid-area: uno;
}

.dos{
	grid-area: dos;
}

.tres{
	grid-area: tres;
}

.cuatro{
	grid-area: cuatro;
}

.cinco{
	grid-area: cinco;
}

.seis{
	grid-area: seis;
}

.siete{
	grid-area: siete;
}

.ocho{
	grid-area: ocho;
}

.nueve{
	grid-area: nueve;
}


.calculadora{
	display: grid;
	grid-template-columns: repeat(4, 1fr);
	grid-auto-rows: repeat(6, 1fr);

	grid-gap: 10px;

	grid-template-areas: 
      "resultado resultado resultado resultado"
      "ce dividir multiplicar restar"
      "siete ocho nueve sumar"
      "cuatro cinco seis sumar"
      "uno dos tres igual"
      "cero cero cero igual";


    border: 7px solid #2a2a2b;

    background: #2a2a2b;
}

.calculadora *{
	min-height: 50px;
}


.scale-up-center {
	-webkit-animation: scale-up-center 0.4s;
	        animation: scale-up-center 0.4s;
}

@-webkit-keyframes scale-up-center {
  0% {
    -webkit-transform: scale(0.5);
            transform: scale(0.5);
  }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}
@keyframes scale-up-center {
  0% {
    -webkit-transform: scale(0.5);
            transform: scale(0.5);
  }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}


.card-header{
	text-align: right;
}

.card-header *{
	margin-left: 10px;
}